import React from 'react';
import { graphql, PageProps } from 'gatsby';
import Layout from '../components/layout';
import SEO from '../components/seo';

type Props = PageProps<GatsbyTypes.NewsArticleBySlugQuery>;

const ArticleTemplate: React.FC<Props> = ({ data }) => {
  const { markdownRemark: article } = data;
  const title = article?.frontmatter?.title;

  return (
    <Layout>
      <SEO title={title ? `${title} | News` : `News`} />

      <article className="py-16">
        <div className="px-4 sm:px-6 lg:px-8">
          <header className="text-lg max-w-prose mx-auto mb-6">
            {article?.frontmatter?.date && (
              <p className="text-lg text-gray-700 text-center leading-8">
                {article.frontmatter.date}
              </p>
            )}
            {article?.frontmatter?.title && (
              <h1 className="mt-2 mb-8 text-3xl text-center leading-8 font-extrabold tracking-tight text-gray-900 sm:text-4xl sm:leading-10">
                {article.frontmatter.title}
              </h1>
            )}
            {article?.frontmatter?.description && (
              <p className="text-xl text-gray-500 leading-8">{article.frontmatter.description}</p>
            )}
          </header>
          {/* eslint-disable react/no-danger */}
          <section
            className="prose prose-lg text-gray-500 mx-auto"
            dangerouslySetInnerHTML={{ __html: article?.html || '' }}
          />
          {/* eslint-enable react/no-danger */}
        </div>
      </article>
    </Layout>
  );
};

export default ArticleTemplate;

export const pageQuery = graphql`
  query NewsArticleBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
      }
    }
  }
`;
